.cur-track-container {
    position: fixed;
    width: 100%;
    height: 70px;
    bottom: 0px;
    background: rgb(255,184,92);
    background: linear-gradient(90deg, rgba(255,184,92,1) 0%, rgba(227,101,136,0.7763480392156863) 63%);
    z-index: 5;
}

.button-bar {
    position: absolute;
    display: inline-block;
    height: 100%;
    width: 170px;
    left: 20%;
    top: -2px;
    justify-content: center;
}

.control-btn {
    position: relative;
    height: 48px;
    width: 48px;
    border-radius: 55%;
    background-color: Transparent;
    background-repeat: no-repeat;
    cursor: pointer;
    overflow: hidden;
    outline:none;
    vertical-align: 50%;
    top: 5px;
    border: 2px whitesmoke solid;
    border: none;
}

/* .control-btn:hover {
    border: 2px #3E2A35 solid;
} */

.fa-step-backward {
    font-size: 24px;
    color: whitesmoke;
}

.fa-step-forward-icon {
    font-size: 24px; 
    color: #F5F5F5;
}

.fa-step-forward:hover {
    color: inherit;
}

.fa-step-backward:hover {
    color: #3E2A35;
}

.pause-svg:hover {
    color: #3E2A35;
}

.prev-track {
    position: absolute;
    top: 18px;
    left: 36px;
    width: 37px;
    height: 37px;
}

.fa-pause {
    font-size: 32px;
    color: white;
}

.fa-pause:hover {
    color: #3E2A35;
}

.play-bar-play {
    position: relative;
    font-size: 34px;
    height: 30px;
    opacity: 1;
    border-radius: 0;
    top: 3px;
    left: 4px;
    color: white;
}

.play-bar-play:hover {
    color: #3E2A35;
}

.pause-svg {
    position: relative;
    height: 3em;
    width: 3em;
    color: black;
}

.toggle-button {
    position: absolute;
    left: 79px;
    top: 14px;
    width: 45px;
    height: 45px;
    color: #352208;
}

.next-button {
    position: absolute;
    top: 18px;
    left: 130px;
    width: 37px;
    height: 37px;
}

.top-container {
    position: relative;
    display: inline-block;
    width: 250px;
    height: 90px;
}

.cur-track-img-container {
    position: relative;
    height: 165px;
    width: 165px;
    border: 1px black solid;
    left: 2px;
    bottom: 100px;
    border-radius: 50%;
    border: 3px pink solid;
    overflow: hidden;
    animation: 50s rotateimg ease-in infinite alternate, 5s changebordercolor linear infinite alternate;
}

.cur-track-img {
    position: relative;
    height: 185px;
    width: 185px;
    bottom: 5px;
    left: -15px;
    border-radius: 50%;
}

@keyframes changebordercolor {
    0% {
       border: 4px orange solid;
    }

    100% {
        border: 4px pink solid;
    }
}

@keyframes rotateimg {
    0% {
        transform: rotate(0deg)
    }

    100% {
        transform: rotate(360deg);
    }
}

