} */

.cur-track-name {
    position: absolute;
    float: right;
    left: 180px;
    top: 13px;
    color: black;
    font-size: 17px;
    width: 185px;
    font-weight: 600;
    text-transform: capitalize;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    /* border: 1px black solid; */
}

.cur-artist-name {
    position: absolute;
    float: right;
    left: 180px;
    top: 40px;
    width: 120px;
    color: #352208;
    font-weight: 500;
    font-size: 14.5px;
    text-transform: capitalize;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    /* border: 1px black solid; */
}

.song-slider {
    position: absolute;
    display: inline-block;
    height: 90px;
    width: 0px;
    left: 34vw;
}

.slider {
    position: relative;
    -webkit-appearance: none;
    width: 430px;
    height: 6px;
    top: 23px;
    left: 73px;
    border-radius: 5px;  
    outline: none;
    opacity: 1;
    -webkit-transition: .2s;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 13px;
    height: 13px;
    border-radius: 50%; 
    background: white;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }

  .song-start-time {
    position: absolute;
    top: 25px;
    left: 27px;
    height: 20px;
    width: 40px;
    color: #352208;
    font-size: 16px;
  }

  .song-end-time {
    position: absolute;
    top: 25px;
    left: 518px;
    height: 20px;
    width: 40px;
    color: #352208;
    font-size: 16px;
}