.tracks-edit {
    position: absolute;
    width: 300px;
    height: 102%;
    top: 5vh;
    /* background-color: white; */
    left: 860px;
    z-index: 5;
}

.trackStyle {
    position: relative;
    display: inline-block;
    height: 72px;
    width: 90%;
    top: 46px;
    left: 40px;
    text-align: center;
    padding-top: 5px;
    /* border: 1px black solid; */
}

.track-popularity {
    left: 550px;
    position: absolute; 
    display: inline-block;
    width: 140px;
    text-align: left;
    line-height: 25px;
    top: 29px;
    font-size: 14px;
    font-weight: 425;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;
    text-transform: capitalize;
    color: grey;
}

.thumbnail-container {
    position: absolute;
    display: inline-block;
    width: 800px;
    height: calc(100vh - 194px);
    top: 2%;
    right: calc((100vw - 820px) / 1.45);
    z-index: 4;
    border-radius: 30px;
    animation: .85s loadanimation ease-out;
}

@keyframes loadanimation {
    0% {
        opacity: 0;
        right: 0px;
    }

    100% {
        opacity: 1;
    }
}

.thumbnail-filter {
    position: absolute;
    width: 105%;
    border-top: 1px #E36588 solid;
    top: 0px;
    height: 43px;
    left: 0px;
    z-index: 6;
    border-radius: 20px;
    background-color: white;
    border-bottom: 3px pink solid;
}

