.darken-bg {
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
}

.darken-bg:hover {
    opacity: 0.8;
}

.chosen-container {
    position: relative;
    justify-content: space-evenly;
    height: calc(99vh - 160px);
    min-height: 700px;
    min-width: 1000px;
    width: 100vw;
    z-index: 2;
    /* border: 1px black solid; */
    border-radius: 10px;
    background-color: whitesmoke;
    /* box-shadow: 0.5px 2px 20px 1px white; */
    font-family: 'Source Sans Pro', sans-serif;
}

.artist-box-container {
    position: absolute;
    display: inline-block;
    box-sizing: content-box;
    padding: 6px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    border-radius: 90px;
    height: 120px;
    top: 230px;
    width: 120px;
    z-index: 2;
    animation: artistboxentrance 1s linear forwards;
    -webkit-animation: artistboxentrance 1s linear forwards;
    /* border: .5px grey solid; */
}
