.slick-prev:before {
    content: "<";
    color: red;
    font-size: 30px;
  }
  
  .slick-next:before {
    content: ">";
    color: red;
    font-size: 30px;
  }

.slick-next {
    position: absolute;
    left: 0px;
    font-size: 35px;
}

.slick-list {
    position: absolute;
    bottom: 2.5vh;
    left: 15.2vw;
    /* border-right: 2px black solid;
