.generate-button {
    content: '';
    cursor: pointer;
    position: absolute;
    width: 25vw;
    height: 50px;
    box-shadow: 1px 2px 5px #888888;
    border-radius: 5px;
    font-size: 28px;
    font-weight: 700;
    text-align: center;
    box-sizing: border-box;
    padding: 6px 10px;
    font-family: 'Open Sans', sans-serif;
    transition: all 1s;
    overflow: hidden;
    z-index: 3;
    animation: 1s hideandshowbutton linear;
}

@keyframes hideandshowbutton {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.generate-button:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: linear-gradient(90deg, rgba(255,184,92,1) 0%, rgba(227,101,136,1) 75%);
    z-index: -1;
    transform: translate(-100%);
    transition: all 1s;
    border-radius: 5px;
}

.generate-button:hover:before {
    transform: translateX(0);
}

.toggle-search-button {
    position: absolute;
    cursor: pointer;
    width: 250px;
    height: 100px;
    box-shadow: 1px 2px 3px #888888;
    border-radius: 5px;
    font-size: 28px;
    font-weight: 700;
    text-align: center;
    box-sizing: border-box;
    padding: 2px 10px;
    font-family: 'Open Sans', sans-serif;
    transition: all 1s;
    overflow: hidden;
    animation: 1s hideandshowbutton linear;
}

.toggle-search-button:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgb(255,184,92);
background: linear-gradient(90deg, rgba(255,184,92,1) 0%, rgba(255,255,255,1) 97%);
    z-index: -1;
    transform: translate(-100%);
    transition: all 1s;
    border-radius: 5px;
}

.toggle-search-button:hover:before {
    transform: translateX(0);
}

.move-into-place-anim {
    position: absolute;
    animation: moveintoplace 1s ease;
    animation-fill-mode: forwards;
}

@keyframes moveintoplace {
    0% {
        opacity: 0.2;
        top: 0px;
    }

    100% {
        height: 150px;
        width: 150px;
        top: -6vh;
        left: 42.5%;
        opacity: 1;
        box-shadow: 1px 2px 10px #3E2A35;
    }
}

.move-out-of-way-anim {
    animation: moveoutofway .6s linear;
    animation-fill-mode: forwards;
}

@keyframes moveoutofway {
    0% {
        opacity: 0.1;
        z-index: -2;
        top: 0px;
    }

    55% {
        z-index: -2;
    }

    99% {
        opacity: 0.8;
    }

    100% {
        opacity: 0.8;
        box-shadow: 1px 2px 5px #3E2A35;
        height: 95px;
        width: 95px;
        top: -6vh;
    } 
}

.move-button-down-anim {
    position: absolute;
    animation: movebuttondown 300ms ease-in;
    animation-fill-mode: forwards;
}

@keyframes movebuttondown {
    0% {
        opacity: 0.0;
        top: 530px;
    }

    95% {
        opacity: 0;
    }

    100% {
        content: ">";
        opacity: 1;
        height: 53px;
        width: 250px;
        z-index: 3;
        top: 15px;
        /* pointer-events: none; */
    }
}

.move-toggle-button-down-anim {
    position: relative;
    animation: movetogglebuttondown .5s ease-out;
    animation-fill-mode: forwards;
}

@keyframes movetogglebuttondown {
    0% {
        opacity: 0.0;
        right: 0px;
        top: -500px;
    }

    90% {
        opacity: 0.001;
        top: 200px;
    }

    100% {
        position: relative;
        margin-left: 100px;
        padding-top: 20px;
        width: 75px;
        height: 80px;
        z-index: 3;
        opacity: 1;
        visibility: hidden;
    }
}

.move-out-middle-right {
